@import "src/common/styles/sass/variables";

.skill {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: $textColor;
  font-family: $TextFontFamily;
  padding: 45px;
}

.icon {
  width: 80px;
  height: 80px;
  background-color: rgba(80, 79, 79, 0.89);
  padding: 10px;
  rotate: 45deg;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;

  & > svg {
    rotate: -45deg;
  }
}

